@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Proza+Libre:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mansalva&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Proza+Libre:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home_container{display:flex;align-items:center}.App{display:flex;flex-direction:row}

video{min-width:100vw;min-height:100vh;max-width:100vw;max-height:100vh;object-fit:fill;right:0;bottom:0;position:fixed;z-index:-2}.video_overlay{background-color:black;opacity:0.5;position:absolute;z-index:-1;width:100vw;height:100vh}

.header_text{font-family:"Mansalva", cursive;text-align:center;font-size:80px}.header_greeting{font-size:80px}.header{z-index:2;width:100vw;display:flex;justify-content:center;color:white;flex-direction:column;align-items:center;top:0}.header_image{border-radius:50%;width:150px}.header_bio{padding:10px;font-size:20px;font-family:"Proza Libre", sans-serif}

.body_container{color:white;display:flex;justify-content:center;font-family:"Proza Libre", sans-serif}

